.remove-container {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    position: relative;
    bottom: 4vh;
}
.remove-text {
    text-align: center;
    font-size: calc(1vw + 2vh);
}
.remove-btn-container {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-gap: calc(2vw + 3vh)
    
}