#top-container {
    display: grid;
    grid-template-columns: 5fr 1fr;
    height: 10vh;
}
#nav-container {
    display: flex;
    justify-content: flex-end;
    font-size: 4vh;
}
.nav {
    border-radius: 1vw;
    grid-area: nav;
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
}
#carousel1 {
    grid-area: carousel1;
}
#carousel2 {
    grid-area: carousel2;
}
#carousel3 {
    grid-area: carousel3;
}
.latest-title {
    grid-area: title;
}