.carousel_view {

  height: 24vh;
  width: 100%;
  background: transparent;
  margin: auto;
  overflow: hidden;
  position: relative;
  z-index: 5;
  
}
.carousel_control {
  position: absolute;
  z-index: 1;
  height: 100%;
  width: 5vw;
  background-color: rgba(221, 221, 221, 0.5);
  transition: background-color 0.3s;
  display: table;
}
.carousel_control .nav-icon {
  display: table-cell;
  vertical-align: middle;
  text-align: center;
}
.carousel_control:hover {
  background-color: rgba(221, 221, 221, 0.8);
}
.carousel_control:first-of-type {
  left: 0;
}
.carousel_control:last-of-type {
  right: 0;
}

.book-list {
  position: absolute;
  margin: 0;
  padding: 0;
  transition: -webkit-transform 0.3s;
  transition: transform 0.3s;
  transition: transform 0.3s, -webkit-transform 0.3s;
  -webkit-transform: translateX(0px);
          transform: translateX(0px);
  list-style: none;
  height: 35vh;
  width: 100%;
  
}
.book-list_item {
  width: 10vw;
  height: 20vh;
  display: inline-block;
}

.book {
  display: table;
  height: 100%;
  width: 15vw;
}
.book span {
  display: table-cell;
  vertical-align: middle;
  text-align: center;
  color: white;
}

.shelf {
  width: 100%;
  height: 5vh;
}

